import {
  RECEIVED_COMMENT,
  ADD_COMMENT,
  EDIT_COMMENT,
  DELETE_COMMENT,
  TOGGLE_Comment_Like,
} from '../actionTypes';

//comments: post popup comments 10,5addcompo render post
const comments = (state = [], action) => {
  switch (action.type) {
    case RECEIVED_COMMENT: {
      let newComments = [...state];
      newComments = action.payload.commentsList;
      return newComments;
    }
    // comments
    case ADD_COMMENT: {
      return [action.payload.comment, ...state];
    }
    case EDIT_COMMENT: {
      let newComments = [...state];
      newComments = action.payload.editedComment;
      return newComments;
    }
    case DELETE_COMMENT: {
      return action.payload.newComments;
    }
    case TOGGLE_Comment_Like: {
      return state.map((comment) => {
        if (comment.commentID === action.payload.id) {
          return { ...comment, likeIssuerID: action.payload.likeIssuerID };
        }
        return comment;
      });
    }
    default:
      return state;
  }
};

export default comments;
