import { nanoid } from 'nanoid';
import { UPDATE_TAG, ADD_TAG } from '../redux/actionTypes';
import { db } from '../firebase';

export const tagProcess = (newTag, tagpostID) => (dispatch, getState) => {
  const { tags } = getState();
  const ref = db.collection('Tag').doc(newTag.value);
  //tag array
  let tagID = [];
  tags.map((tag) => {
    tagID.push(tag.value);
  });

  //tagtagpostID array
  if (tagID.includes(newTag.value)) {
    let updateTag = tags.find((tag) => tag.value === newTag.value);
    let updateTagpostID = updateTag.postID;
    updateTagpostID.push(tagpostID);

    dispatch({
      type: UPDATE_TAG,
      payload: { tags },
    });
    // firebase postID array
    const ref = db.collection('Tag').doc(newTag.value);
    ref.update({
      postID: updateTagpostID,
    });
  } else {
    //tagstate data tag state
    const tagData = {
      postID: [tagpostID],
      label: newTag.label,
      value: newTag.value,
    };

    dispatch({
      type: ADD_TAG,
      payload: { tagData },
    });

    //tagValue
    const ref = db.collection('Tag').doc(newTag.value);
    ref.set(tagData).then(() => {});
  }
};
